let ROOT = "https://api2.httpiness.com";

export function reportDownloadWin32()
{
    if (!localStorage.getItem("download")) {
        localStorage.setItem("download", true);
        fetch(`${ROOT}/report/download/win32`);
    }
}

export function reportDownloadMacOs()
{
    if (!window.localStorage.getItem("download")) {
        window.localStorage.setItem("download", true);
        fetch(`${ROOT}/report/download/macos`);
    }
}

export function reportHomeVisit()
{
    fetch(`${ROOT}/report/home/visit`);
}

