<script>
	import { onMount, onDestroy } from 'svelte';

    let notOnTop = false;

    const handleScroll = () => {
        if (window.scrollY == 0)
            notOnTop = false;
        else
            notOnTop = true;
    };

    onMount(() => window.addEventListener("scroll", handleScroll));
    onDestroy(() => window.addEventListener("scroll", handleScroll));
</script>

